@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,900&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  background: #111117;
  color: #fff;
}

.heatmap_wrapper {
  position: fixed;
  width: 100%;
  height: 650px;
  background-image: url(/heatmap/static/media/bg-image.0651d2a2.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position-y: -20px;
}

.heatmap_stats {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
}

.stat {
  display: flex;
  flex-basis: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid #fff;
}

.stat_label {
  font-weight: 300;
}

.stat_value {
  font-weight: 900;
  font-size: 24px;
}

.heatmap_container {
  width: 100%;
  height: 450px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.heatmap_fighter {
  width: 100%;
  height: 100%;
  background-image: url(/heatmap/static/media/maleRed.b9f7baea.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  position: relative;
}

.heatmap_head {
  width: 100%;
  height: 100%;
  background-image: url(/heatmap/static/media/heat_headMale.47d51d52.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  position: absolute;
  top: 0;
  left: 0;
}

.heatmap_body {
  width: 100%;
  height: 100%;
  background-image: url(/heatmap/static/media/heat_bodyMale.02ac589f.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  position: absolute;
  top: 0;
  left: 0;
}

.heatmap_legs {
  width: 100%;
  height: 100%;
  background-image: url(/heatmap/static/media/heat_legsMale.833935d5.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  position: absolute;
  top: 0;
  left: 0;
}

.heatmap_buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  padding: 10px 0;
  font-weight: 700;
  color: #111117;
  background: #111117;
}

.reg_strike {
  flex-basis: 30%;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: orange;
  margin-bottom: 10px;
}

.sig_strike {
  flex-basis: 30%;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: red;
}

.settings_wrapper {
  width: 100%;
  padding: 20px;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  position: fixed;
  top: 650px;
  background: #111117;
}

.setting {
  margin-bottom: 10px;
}
